.projects-container {
    text-align: center;
    padding: 80px 20px;
    background: linear-gradient(135deg, #0d1117, #1a1f25);
    color: #e6edf3;
}

h2 {
    font-size: 2.5rem;
    margin-bottom: 30px;
    text-transform: uppercase;
    letter-spacing: 2px;
    position: relative;
    display: inline-block;
}

h2::after {
    content: "";
    display: block;
    width: 60px;
    height: 4px;
    background: #007acc;
    margin: 10px auto;
    border-radius: 2px;
}

.projects-grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
    max-width: 1000px;
    margin: 0 auto;
}

.project-card {
    background: rgba(31, 41, 55, 0.7);
    padding: 30px;
    border-radius: 16px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 250px;
    height: 200px;
    box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.5);
    transition: transform 0.4s ease-in-out, box-shadow 0.4s ease-in-out;
    border: 2px solid transparent;
    background-clip: padding-box;
}

.project-card:hover {
    transform: translateY(-8px);
    box-shadow: 0px 0px 20px rgba(0, 122, 204, 0.9);
    border: 2px solid rgba(0, 122, 204, 0.8);
}

.project-title {
    font-size: 1.4rem;
    font-weight: bold;
}

.project-type {
    font-size: 1rem;
    color: #b0b3b8;
    margin-top: 5px;
}

.project-btn {
    margin-top: 10px;
    padding: 10px 20px;
    font-size: 1rem;
    border: none;
    border-radius: 8px;
    background: #007acc;
    color: white;
    cursor: pointer;
    transition: background 0.3s ease-in-out;
}

.project-btn:hover {
    background: #00d4ff;
}
